.hot-products {
  //margin-left: -25px;
  //margin-right: -25px;
  padding-bottom: 70px;
}

.hot-products_list {
  display: flex;
  justify-content: space-between;
}

.hot-products_item {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;

  &--onsale {
    position: relative;
    &::before {
      position: absolute;
      top: 25px;
      left: 33px;
      content: "30%";
      width: 52px;
      height: 24px;
      text-align: center;
      border-radius: 15px;
      color: var(--c-white);
      background-color: var(--c-red);
    }
  }

  p {
    margin: 0;
  }

  @media (max-width: 1099px) {
    &:last-of-type {
      display: none;
    }
  }

  @media (max-width: 600px) {
    &:nth-of-type(3) {
      display: none;
    }
  }

  @media (max-width: 479px) {
    &:nth-of-type(2) {
      display: none;
    }
  }
}
